.Report-Container {
  display: grid;
  max-width: 600px;
  margin: 0 auto;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: 175px 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.Report-Header-Img {
  grid-area: 1 / 1 / 2 / 3;
  align-self: center;
}
.Report-Header-Img img {
  width: 100%;
  height: auto;
}

.Report-Header-Title {
  grid-area: 1 / 3 / 2 / 11;
  text-align: center;
  align-self: center;
}
.Report-Header-Title h1 span {
  font-size: 0.75em;
}
.Report-Header-Title h1 {
  font-size: 2em;
}
.Report-Header-Title h2 {
  font-size: 1em;
  color: #2b2b2b;
}

.Report-Data {
  grid-area: 2 / 1 / 3 / 11;
  text-align: center;
}
.Report-Data h3 {
  margin: 10px 0 0 0;
  text-shadow: 1px 1px 1px #fff;
}

.Report-Data hr {
  height: 0px;
  margin: 5px;
  border-top: 1px solid #cccccc;
  border-bottom: 1px solid #ffffff;
}

.Report-Data table {
  margin: 20px auto;
  background: linear-gradient(to right, #a74cf2, #617bfb);
  border-radius: 5px;
  border-color: gray;
  color: white;
  font-size: 1.2em;
  border-collapse: separate;
  border-spacing: 10px 5px;
  padding: 10px 25px;
}
.Report-Data table tr > td:first-child {
  text-align: right;
}
.Report-Data table tr > td:nth-child(2) {
  text-align: center;
  font-weight: bold;
  margin-left: 25px;
}

canvas {
  max-width: 100%;
  max-height: 100%;
}

